import React, { useState, useEffect } from "react"
import { Card } from "react-bootstrap"
import {
  createrapportInst,
  getAppointmentDetails,
} from "../actions/scheduleActions"
import { useDispatch, useSelector } from "react-redux"
import { CREATE_RAPPORT_RESET } from "../constants/rapportConstants"
import { useParams } from "react-router-dom"
import { Container } from "react-bootstrap"
import FileBase from "react-file-base64"

const RapportScreen = ({ history }) => {
  const { id } = useParams()
  //console.log(id)
  const [description, setDescription] = useState("")
  const [cours, setCours] = useState("")
  const dispatch = useDispatch()

  const createRapport = useSelector((state) => state.createRapport)
  const { success, loading, error } = createRapport

  const appointmentDetail = useSelector((state) => state.appointmentDetail)
  const { appointment } = appointmentDetail

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const { instructorInfo } = instructorLogin

  useEffect(() => {
    if (success) {
      setDescription("")
      history.push("/instructor-lessons")
    }
  }, [dispatch, success])

  useEffect(() => {
    dispatch(getAppointmentDetails(id))
  }, [dispatch])

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(createrapportInst(id, { description, cours }))
  }

  return (
    <div className="bg-gray-100  text-white h-screen">
      <Container>
        <div className="flex flex-col text-center w-full mb-12">
          <h1 className="sm:text-5xl text-2xl font-semibold title-font mb-4 mt-5 text-black">
            Report
          </h1>
          {/* <p className="lg:w-2/3 mx-auto leading-relaxed text-black font-semibold">
            write a report 
          </p> */}
        </div>
        {appointment &&
          appointment.rapport.map((report) => (
            <div className="pb-5 w-full" key={report._id}>
              <div className="relative">
                <textarea
                  value={report.description}
                  onChange={(e) => setDescription(e.target.value)}
                  className="w-full h-96 bg-gray-50 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 h-32 text-base outline-none text-gray-700 py-1 px-3 resize-none leading-6 transition-colors duration-200 ease-in-out"
                ></textarea>
              </div>
              <Card>
                {console.log(report.cours)}
                <Card.Img src={report.cours} style={{ height: "220px" }} />
              </Card>
            </div>
          ))}
        {appointment && appointment.rapport.lenth !== 0 && instructorInfo && (
          <form onSubmit={submitHandler}>
            <div className="pb-5 w-full">
              <div className="relative">
                <textarea
                  value={description}
                  onChange={(e) => setDescription(e.target.value)}
                  className="w-full h-96 bg-gray-50 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 h-32 text-base outline-none text-gray-700 py-1 px-3 resize-none leading-6 transition-colors duration-200 ease-in-out"
                ></textarea>
              </div>
              <FileBase
                type="file"
                multiple={false}
                onDone={({ base64 }) => setCours(base64)}
              />
            </div>
            <div className="pb-5 w-full">
              <button
                type="submit"
                className="flex mx-auto text-black bg-gray-400 border-0 py-2 px-8 rounded text-lg"
              >
                Send
              </button>
            </div>
          </form>
        )}
      </Container>
    </div>
  )
}

export default RapportScreen
