import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Row, Col, ListGroup, Image, Card, Button } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { getEnrollmentDetails, pay } from "../actions/enrollmentActions"
import moment from "moment"
import axios from "axios"
import { PayPalButton } from "react-paypal-button-v2"
import { ORDER_PAY_RESET } from "../constants/enrollmentConstants"

const LessonsScreen = ({ match, history }) => {
  const enrollmentId = match.params.id
  const [sdkReady, setSdkReady] = useState(false)

  const dispatch = useDispatch()

  const enrollmentDetails = useSelector((state) => state.enrollmentDetails)
  const { enrollment, loading, error } = enrollmentDetails

  const enrollmentPay = useSelector((state) => state.enrollmentPay)
  const { loading: loadingPay, success: successPay } = enrollmentPay

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  useEffect(() => {
    if (!userInfo) {
      history.push("/login")
    }

    const addPayPalScript = async () => {
      const { data: clientId } = await axios.get("/api/config/paypal")
      const script = document.createElement("script")
      script.type = "text/javascript"
      script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
      script.async = true
      script.onload = () => {
        setSdkReady(true)
      }
      document.body.appendChild(script)
    }

    if (!enrollment || successPay || enrollment._id !== enrollmentId) {
      dispatch({ type: ORDER_PAY_RESET })
      dispatch(getEnrollmentDetails(enrollmentId))
    } else if (!enrollment.isPaid) {
      if (!window.paypal) {
        addPayPalScript()
      } else {
        setSdkReady(true)
      }
    }
  }, [dispatch, enrollmentId, enrollment])

  const successPaymentHandler = (paymentResult) => {
    console.log(paymentResult)
    dispatch(pay(enrollmentId, paymentResult))
  }

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <>
      <h1 className="ml-3">Lessons </h1>
      <Row>
        <Col md={8}>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h2 className="ml-3">Student</h2>
              <p>
                <strong>Name: </strong> {userInfo.firstName} {userInfo.lastName}
              </p>
              <p>
                <strong>Email: </strong>{" "}
                <a href={`mailto:${enrollment.user.email}`}>
                  {enrollment.user.email}
                </a>
              </p>
            </ListGroup.Item>
            <ListGroup.Item>
              <h2>Lessons</h2>
              {enrollment.bookings.length === 0 ? (
                <Message>No Lessons</Message>
              ) : (
                <ListGroup variant="flush">
                  {enrollment.bookings.map((item, index) => (
                    <ListGroup.Item key={index}>
                      <Row>
                        <Col>{item.title}</Col>
                        <Col>{item.desc}</Col>
                        <Col>
                          {moment(item.start).format("MMMM Do YYYY, h:mm:ss a")}
                        </Col>
                        <Col>
                          {moment(item.start).format("MMMM Do YYYY, h:mm:ss a")}
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              )}
            </ListGroup.Item>
          </ListGroup>
        </Col>
        <Col md={4}>
          <Card>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h2>Order Summary</h2>
              </ListGroup.Item>

              <ListGroup.Item>
                <Row>
                  <Col>Total</Col>
                  <Col>${enrollment.totalPrice}</Col>
                </Row>
              </ListGroup.Item>
              {!enrollment.isPaid && (
                <ListGroup.Item>
                  {loadingPay && <Loader />}
                  {!sdkReady ? (
                    <Loader />
                  ) : (
                    <PayPalButton
                      amount={enrollment.totalPrice}
                      onSuccess={successPaymentHandler}
                    />
                  )}
                </ListGroup.Item>
              )}
            </ListGroup>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default LessonsScreen
