import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Form, Button, Row, Col } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import Message from "../components/Message"
import Loader from "../components/Loader"
import FormContainer from "../components/FormContainer"
import { login, register } from "../actions/userActions"
import "./style.css"
import registers from "../img/register.svg"
import log from "../img/log.svg"

const Schedule = ({ location, history }) => {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")

  const [firstName, setFirstName] = useState("")
  const [lastName, setLastName] = useState("")
  const [confirmPassword, setConfirmPassword] = useState("")
  const [phone, setPhone] = useState("")
  const [message, setMessage] = useState(null)

  const dispatch = useDispatch()

  const userLogin = useSelector((state) => state.userLogin)
  const { loading, error, userInfo } = userLogin

  const userRegister = useSelector((state) => state.userRegister)
  const { loading: load, error: err, userInfo: userI } = userRegister

  const redirect = location.search
    ? location.search.split("=")[1]
    : "/dashboard"

  useEffect(() => {
    if (userInfo && !userInfo.isAdmin) {
      history.push(redirect)
    } else if (userInfo && userInfo.isAdmin) {
      history.push("/admin-dashboard")
    }
  }, [history, userInfo, redirect])

  useEffect(() => {
    if (userI) {
      history.push(redirect)
    }
  }, [history, userI, redirect])

  const submitHandlerRegister = (e) => {
    e.preventDefault()
    if (password !== confirmPassword) {
      setMessage("Passwords do not match")
    } else {
      dispatch(register(firstName, lastName, phone, email, password))
    }
  }

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(login(email, password))
  }

  const [sign, setSign] = useState(false)
  return (
    <div className={sign ? "sign-up-mode container-sign" : "container-sign"}>
      <div className="forms-container">
        <div className="signin-signup">
          <form className="sign-in-form forms" onSubmit={submitHandler}>
            <h2 className="title">Sign in</h2>
            {error && <Message variant="danger">{error}</Message>}
            {loading && <Loader />}
            <div className="input-field inputs">
              <i className="fas fa-user"></i>
              <input
                type="text"
                placeholder="Username"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="input-field inputs">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Login" className="btns solid" />
            {/* <p className="social-text">Or Sign in with social platforms</p>
            <div className="social-media">
              <a href="#" className="social-icon">
                <i className="fab fa-facebook-f"></i>
              </a>
              <a href="#" className="social-icon">
                <i className="fab fa-twitter"></i>
              </a>
              <a href="#" className="social-icon">
                <i className="fab fa-google"></i>
              </a>
              <a href="#" className="social-icon">
                <i className="fab fa-linkedin-in"></i>
              </a>
            </div> */}
          </form>
          {message && <Message variant="danger">{message}</Message>}
          {err && <Message variant="danger">{err}</Message>}
          {load && <Loader />}
          <form className="sign-up-form forms" onSubmit={submitHandlerRegister}>
            <h2 className="title">Sign up</h2>
            <div className="input-field inputs">
              <i className="fas fa-user"></i>
              <input
                type="text"
                placeholder="first Name"
                value={firstName}
                onChange={(e) => setFirstName(e.target.value)}
              />
            </div>
            <div className="input-field inputs">
              <i className="fas fa-user"></i>
              <input
                type="text"
                placeholder="last Name"
                value={lastName}
                onChange={(e) => setLastName(e.target.value)}
              />
            </div>
            <div className="input-field inputs">
              <i className="fas fa-envelope"></i>
              <input
                type="email"
                placeholder="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="input-field inputs">
              <i className="fas fa-phone"></i>
              <input
                type="number"
                placeholder="Phone"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="input-field inputs">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <div className="input-field inputs">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                placeholder="Confirm password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
              />
            </div>
            <input type="submit" className="btns" value="Sign up" />
            {/* <p className="social-text">Or Sign up with social platforms</p>
            <div className="social-media">
              <a href="#" className="social-icon">
                <i className="fab fa-facebook-f"></i>
              </a>
              <a href="#" className="social-icon">
                <i className="fab fa-twitter"></i>
              </a>
              <a href="#" className="social-icon">
                <i className="fab fa-google"></i>
              </a>
              <a href="#" className="social-icon">
                <i className="fab fa-linkedin-in"></i>
              </a>
            </div> */}
          </form>
        </div>
      </div>

      <div className="panels-container">
        <div className="panel left-panel">
          <div className="content">
            <h3>New here ?</h3>
            <p>join us for a wonderful learning experience </p>
            <button
              className="btns transparent"
              id="sign-up-btn"
              onClick={() => setSign(true)}
            >
              Sign up
            </button>
          </div>
          <img src={log} className="image" alt="" />
        </div>
        <div className="panel right-panel">
          <div className="content">
            <h3>One of us ?</h3>
            <p>if you already have an account just sign in and join us </p>
            <button
              className="btns transparent"
              id="sign-in-btn"
              onClick={() => setSign(false)}
            >
              Sign in
            </button>
          </div>
          <img src={registers} className="image" alt="" />
        </div>
      </div>
    </div>
  )
}

export default Schedule
