import React, { useEffect } from "react"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { useDispatch, useSelector } from "react-redux"
import {
  Container,
  Row,
  Col,
  ListGroup,
  Form,
  Button,
  Image,
  ButtonGroup,
} from "react-bootstrap"
import { listInstructors } from "../actions/instructorActions"
import Instructor from "../components/Instructor"
import Paginate from "../components/Paginate"

const DashboardScreen = ({ match }) => {
  const keyword = match.params.keyword
  const pageNumber = match.params.pageNumber || 1
  const dispatch = useDispatch()

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  const instructorList = useSelector((state) => state.instructorList)
  const { loading, error, instructors, page, pages } = instructorList

  useEffect(() => {
    dispatch(listInstructors(keyword, pageNumber))
  }, [dispatch, keyword, pageNumber])

  return (
    <>
      <Container fluid>
        <Row>
          <Col xs={3}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                {userInfo ? (
                  <h3 className="d-flex justify-content-center align-items-center">
                    {" "}
                    {userInfo.firstName} {userInfo.lastName}
                  </h3>
                ) : null}
              </ListGroup.Item>

              <ListGroup.Item>
                <div className="d-flex justify-content-center align-items-center pb-4 pt-3">
                  <Image
                    src="https://www.hoorayschoolbali.com/wp-content/uploads/2020/03/elearning.jpg"
                    fluid
                    rounded
                    style={{ height: "150px", height: "150px" }}
                  />
                </div>
              </ListGroup.Item>
              <ListGroup>
                <Button className="btn-block" type="button">
                  Book a lesson
                </Button>
                <div className="pt-3">
                  <ButtonGroup
                    vertical
                    className="d-flex justify-content-center align-items-center"
                  >
                    <Button variant="link" href="/profile">
                      My Profile
                    </Button>
                    <div className="pt-2" />
                    <Button variant="link" href="/favorite-tutors">
                      Favorite Tutors
                    </Button>
                    <div className="pt-2" />
                    <Button variant="link" href="/student-lessons">
                      Booked Lessons
                    </Button>
                  </ButtonGroup>
                </div>
              </ListGroup>
            </ListGroup>
          </Col>

          <Col xs={9}>
            {loading ? (
              <Loader />
            ) : error ? (
              <Message variant="danger">{error}</Message>
            ) : (
              <>
                <Row>
                  {instructors.instructors &&
                    instructors.instructors.map((instructor) => (
                      <Col key={instructor._id} sm={12} md={6} lg={4} xl={3}>
                        <Instructor
                          key={instructor._id}
                          instructor={instructor}
                        />
                      </Col>
                    ))}
                </Row>
                <Paginate
                  pages={pages}
                  page={page}
                  keyword={keyword ? keyword : ""}
                />
              </>
            )}
          </Col>
        </Row>
      </Container>
    </>
  )
}

export default DashboardScreen
