import {
  CREATE_ENROLLMENT_REQUEST,
  CREATE_ENROLLMENT_SUCCESS,
  CREATE_ENROLLMENT_FAIL,
  CREATE_ENROLLMENT_RESET,
  ENROLLMENT_DETAILS_REQUEST,
  ENROLLMENT_DETAILS_SUCCESS,
  ENROLLMENT_DETAILS_FAIL,
  ENROLLMENT_LIST_MY_REQUEST,
  ENROLLMENT_LIST_MY_SUCCESS,
  ENROLLMENT_LIST_MY_FAIL,
  ENROLLMENT_LIST_MY_RESET,
  ENROLLMENT_LIST_REQUEST,
  ENROLLMENT_LIST_SUCCESS,
  ENROLLMENT_LIST_FAIL,
  ORDER_PAY_FAIL,
  ORDER_PAY_SUCCESS,
  ORDER_PAY_REQUEST,
  ORDER_PAY_RESET,
  CREATE_RAPPORT_REQUEST,
  CREATE_RAPPORT_SUCCESS,
  CREATE_RAPPORT_FAIL,
  CREATE_RAPPORT_RESET,
} from "../constants/enrollmentConstants"

export const enrollmentCreateReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_ENROLLMENT_REQUEST:
      return {
        loading: true,
      }
    case CREATE_ENROLLMENT_SUCCESS:
      return {
        loading: false,
        success: true,
        enrollment: action.payload,
      }
    case CREATE_ENROLLMENT_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case CREATE_ENROLLMENT_RESET:
      return {}
    default:
      return state
  }
}

export const enrollmentDetailsReducer = (
  state = { loading: true, bookings: [] },
  action
) => {
  switch (action.type) {
    case ENROLLMENT_DETAILS_REQUEST:
      return {
        ...state,
        loading: true,
      }
    case ENROLLMENT_DETAILS_SUCCESS:
      return {
        loading: false,
        enrollment: action.payload,
      }
    case ENROLLMENT_DETAILS_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

export const enrollmentListMyReducer = (
  state = { enrollments: [] },
  action
) => {
  switch (action.type) {
    case ENROLLMENT_LIST_MY_REQUEST:
      return {
        loading: true,
      }
    case ENROLLMENT_LIST_MY_SUCCESS:
      return {
        loading: false,
        enrollments: action.payload,
      }
    case ENROLLMENT_LIST_MY_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case ENROLLMENT_LIST_MY_RESET:
      return { enrollments: [] }
    default:
      return state
  }
}

export const enrollmentListReducer = (state = { enrollments: [] }, action) => {
  switch (action.type) {
    case ENROLLMENT_LIST_REQUEST:
      return {
        loading: true,
      }
    case ENROLLMENT_LIST_SUCCESS:
      return {
        loading: false,
        enrollments: action.payload,
      }
    case ENROLLMENT_LIST_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

export const enrollmentPayReducer = (state = {}, action) => {
  switch (action.type) {
    case ORDER_PAY_REQUEST:
      return {
        loading: true,
      }
    case ORDER_PAY_SUCCESS:
      return {
        loading: false,
        success: true,
      }
    case ORDER_PAY_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    case ORDER_PAY_RESET:
      return {}
    default:
      return state
  }
}

export const createRapportReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_RAPPORT_REQUEST:
      return { loading: true }
    case CREATE_RAPPORT_SUCCESS:
      return { loading: false, success: true }
    case CREATE_RAPPORT_FAIL:
      return { loading: false, error: action.payload }
    case CREATE_RAPPORT_RESET:
      return {}
    default:
      return state
  }
}
