import axios from "axios"
import {
  INSTRUCTOR_CREATE_APPOINTMENT_REQUEST,
  INSTRUCTOR_CREATE_APPOINTMENT_SUCCESS,
  INSTRUCTOR_CREATE_APPOINTMENT_FAIL,
  INSTRUCTOR_APPOINTMENT_DETAIL_REQUEST,
  INSTRUCTOR_APPOINTMENT_DETAIL_SUCCESS,
  INSTRUCTOR_APPOINTMENT_DETAIL_FAIL,
  ADD_APPOINTMENT,
  REMOVE_APPOINTMENT,
  CLEAR_APPOINTMENT,
  GET_APPOINTMENTS_REQUEST,
  GET_APPOINTMENTS_SUCCESS,
  GET_APPOINTMENTS_FAIL,
  APPOINTMENT_DELETE_REQUEST,
  APPOINTMENT_DELETE_SUCCESS,
  APPOINTMENT_DELETE_FAIL,
  APPOINTMENT_UPDATE_REQUEST,
  APPOINTMENT_UPDATE_SUCCESS,
  APPOINTMENT_UPDATE_FAIL,
  APPOINTMENT_LIST_REQUEST,
  APPOINTMENT_LIST_SUCCESS,
  APPOINTMENT_LIST_FAIL,
} from "../constants/scheduleConstants"
import { logoutInstructor } from "./instructorActions"
import {
  CREATE_RAPPORT_REQUEST,
  CREATE_RAPPORT_SUCCESS,
  CREATE_RAPPORT_FAIL,
} from "../constants/enrollmentConstants"

export const createAppointmentInst = (appointment) => async (
  dispatch,
  getState
) => {
  try {
    dispatch({ type: INSTRUCTOR_CREATE_APPOINTMENT_REQUEST })
    const {
      instructorLogin: { instructorInfo },
    } = getState()
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${instructorInfo.token}`,
      },
    }
    const { data } = await axios.post(`/api/appointment`, appointment, config)

    dispatch({
      type: INSTRUCTOR_CREATE_APPOINTMENT_SUCCESS,
      payload: data,
    })
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      dispatch(logoutInstructor())
    }
    dispatch({
      type: INSTRUCTOR_CREATE_APPOINTMENT_FAIL,
      payload: message,
    })
  }
}

