export const updateAppointment = (appointment) => async (
  dispatch,
  getState
) => {
  try {
    dispatch({
      type: APPOINTMENT_UPDATE_REQUEST,
    })

    const {
      instructorLogin: { instructorInfo },
    } = getState()
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${instructorInfo.token}`,
      },
    }

    const { data } = await axios.put(
      `/api/appointment/${appointment._id}`,
      appointment,
      config
    )

    dispatch({
      type: APPOINTMENT_UPDATE_SUCCESS,
      payload: data,
    })
    dispatch({ type: GET_APPOINTMENTS_SUCCESS, payload: data })
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      dispatch(logoutInstructor())
    }
    dispatch({
      type: APPOINTMENT_UPDATE_FAIL,
      payload: message,
    })
  }
}

