import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Row, Col, ListGroup, Image, Card, Button } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import Message from "../components/Message"
import Loader from "../components/Loader"
import moment from "moment"
import { listenrollments } from "../actions/enrollmentActions"
import { getInstAppointments } from "../actions/scheduleActions"

const InstructorLessons = () => {
  const [booking, setBooking] = useState([])
  const dispatch = useDispatch()

  const enrollmentList = useSelector((state) => state.enrollmentList)
  const { loading, error, enrollments } = enrollmentList

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const {
    loading: loadingInst,
    error: errorInst,
    instructorInfo,
  } = instructorLogin

  const appointmentsDetails = useSelector((state) => state.appointmentsDetails)
  const { appointment: app, loading: load, error: err } = appointmentsDetails

  //   if (enrollments) {
  //     for (let i = 0; i < enrollments.length; i++) {
  //       //console.log(enrollments[i].bookings)
  //       const lesson =
  //         enrollments[i].bookings &&
  //         enrollments[i].bookings.map((less) => {
  //           return less.schedule
  //         })

  //       if (app) {
  //         for (let j = 0; j < app.length; j++) {
  //           if (lesson.includes(app[j]._id)) {
  //             setBooking(enrollments[i].bookings)

  //             console.log(app[i])
  //           }
  //         }
  //       }
  //     }
  //   }

  useEffect(() => {
    if (enrollments) {
      let ids = []
      let lessons = []
      for (let i = 0; i < enrollments.length; i++) {
        //console.log(enrollments[i].bookings)
        const lesson =
          enrollments[i].bookings &&
          enrollments[i].bookings.map((less) => {
            ids.push(less.schedule)
            lessons.push(less)
            return less.schedule
          })

        if (app) {
          for (let j = 0; j < app.length; j++) {
            ids = ids.filter((id) => id === app[j]._id)
            // console.log(ids)
            for (let k = 0; k < ids.length; k++) {
              lessons = lessons.filter((le) => le.schedule === ids[k])
            }
            //console.log(lessons)
            //lessons = lessons.filter((le) => le.schedule === ids[0])
            if (ids.includes(app[j]._id)) {
              setBooking(lessons)
            }
          }
        }
      }
    }
  }, [enrollments, app])

  useEffect(() => {
    if (instructorInfo) {
      dispatch(listenrollments())
    }

    if (instructorInfo && !app) {
      dispatch(getInstAppointments())
    }
  }, [dispatch, instructorInfo])

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <>
      <h1 className="ml-3">Lessons </h1>
      <Row>
        <Col md={12} className="bg-gray-500">
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h2 className="ml-3">Tutor</h2>
              <p>
                <strong>Name: </strong> {instructorInfo.firstName}{" "}
                {instructorInfo.lastName}
              </p>
            </ListGroup.Item>
            <ListGroup.Item>
              <h2>Lessons</h2>
              {booking.length === 0 ? (
                <Message>No Lessons</Message>
              ) : (
                <ListGroup variant="flush">
                  {booking.map((item, index) => (
                    <ListGroup.Item key={index}>
                      <Row>
                        <Col md={1}>{item.title}</Col>
                        <Col md={1}>{item.desc}</Col>
                        <Col md={3}>
                          {moment(item.start).format("MMMM Do YYYY, h:mm:ss a")}
                        </Col>
                        <Col md={3}>
                          {moment(item.start).format("MMMM Do YYYY, h:mm:ss a")}
                        </Col>

                        <Col md={2}>
                          <Button href={`/video/${item._id}`}>
                            Go to meeting
                          </Button>
                        </Col>
                        <Col md={2}>
                          <Button
                            href={`${item.schedule}/rapport`}
                            className="btn btn-light"
                          >
                            Rapport
                          </Button>
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              )}
            </ListGroup.Item>
          </ListGroup>
        </Col>
      </Row>
    </>
  )
}

export default InstructorLessons
