import axios from "axios"
import {
  INSTRUCTOR_DETAILS_FAIL,
  INSTRUCTOR_DETAILS_REQUEST,
  INSTRUCTOR_DETAILS_SUCCESS,
  INSTRUCTOR_LOGIN_FAIL,
  INSTRUCTOR_LOGIN_REQUEST,
  INSTRUCTOR_LOGIN_SUCCESS,
  INSTRUCTOR_LOGOUT,
  INSTRUCTOR_REGISTER_FAIL,
  INSTRUCTOR_REGISTER_REQUEST,
  INSTRUCTOR_REGISTER_SUCCESS,
  INSTRUCTOR_UPDATE_PROFILE_FAIL,
  INSTRUCTOR_UPDATE_PROFILE_REQUEST,
  INSTRUCTOR_UPDATE_PROFILE_SUCCESS,
  INSTRUCTOR_DETAILS_RESET,
  INSTRUCTOR_LIST_FAIL,
  INSTRUCTOR_LIST_SUCCESS,
  INSTRUCTOR_LIST_REQUEST,
  INSTRUCTOR_LIST_RESET,
  INSTRUCTOR_DELETE_REQUEST,
  INSTRUCTOR_DELETE_SUCCESS,
  INSTRUCTOR_DELETE_FAIL,
  INSTRUCTOR_UPDATE_FAIL,
  INSTRUCTOR_UPDATE_SUCCESS,
  INSTRUCTOR_UPDATE_REQUEST,
  INSTRUCTOR_CREATE_REVIEW_REQUEST,
  INSTRUCTOR_CREATE_REVIEW_SUCCESS,
  INSTRUCTOR_CREATE_REVIEW_FAIL,
  INSTRUCTOR_CREATE_REVIEW_RESET,
  FAVORITE_INSTRUCTOR,
} from "../constants/instructorConstants"
import { logout } from "./userActions"

export const loginInstructor = (email, password) => async (dispatch) => {
  try {
    dispatch({
      type: INSTRUCTOR_LOGIN_REQUEST,
    })

    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    }

    const { data } = await axios.post(
      "/api/instructors/login",
      { email, password },
      config
    )

    dispatch({
      type: INSTRUCTOR_LOGIN_SUCCESS,
      payload: data,
    })

    localStorage.setItem("instructorInfo", JSON.stringify(data))
  } catch (error) {
    dispatch({
      type: INSTRUCTOR_LOGIN_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}
export const logoutInstructor = () => (dispatch) => {
  localStorage.removeItem("instructorInfo")

  dispatch({ type: INSTRUCTOR_LOGOUT })
  dispatch({ type: INSTRUCTOR_DETAILS_RESET })
  dispatch({ type: INSTRUCTOR_LIST_RESET })
  document.location.href = "/login"
}

export const registerInstructor = (
  firstName,
  lastName,
  email,
  password,
  nationality,
  description,
  speciality,
  experience,
  image
) => async (dispatch) => {
  try {
    dispatch({
      type: INSTRUCTOR_REGISTER_REQUEST,
    })

    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    }

    const { data } = await axios.post(
      "/api/instructors",
      {
        firstName,
        lastName,
        email,
        password,
        nationality,
        description,
        speciality,
        experience,
        image,
      },
      config
    )

    dispatch({
      type: INSTRUCTOR_REGISTER_SUCCESS,
      payload: data,
    })

    dispatch({
      type: INSTRUCTOR_LOGIN_SUCCESS,
      payload: data,
    })

    localStorage.setItem("instructorInfo", JSON.stringify(data))
  } catch (error) {
    dispatch({
      type: INSTRUCTOR_REGISTER_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    })
  }
}

export const getInstructorDetails = (id) => async (dispatch, getState) => {
  try {
    dispatch({
      type: INSTRUCTOR_DETAILS_REQUEST,
    })

    const { data } = await axios.get(`/api/instructors/${id}`)

    dispatch({
      type: INSTRUCTOR_DETAILS_SUCCESS,
      payload: data,
    })
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      //dispatch(logoutInstructor())
    }
    dispatch({
      type: INSTRUCTOR_DETAILS_FAIL,
      payload: message,
    })
  }
}

export const updateInstructorProfile = (instructor) => async (
  dispatch,
  getState
) => {
  try {
    dispatch({
      type: INSTRUCTOR_UPDATE_PROFILE_REQUEST,
    })

    const {
      instructorLogin: { instructorInfo },
    } = getState()

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${instructorInfo.token}`,
      },
    }

    const { data } = await axios.put(
      `/api/instructors/profile`,
      instructor,
      config
    )

    dispatch({
      type: INSTRUCTOR_UPDATE_PROFILE_SUCCESS,
      payload: data,
    })
    dispatch({
      type: INSTRUCTOR_LOGIN_SUCCESS,
      payload: data,
    })
    localStorage.setItem("instructorInfo", JSON.stringify(data))
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      dispatch(logoutInstructor())
    }
    dispatch({
      type: INSTRUCTOR_UPDATE_PROFILE_FAIL,
      payload: message,
    })
  }
}

