import {
  INSTRUCTOR_CREATE_APPOINTMENT_REQUEST,
  INSTRUCTOR_CREATE_APPOINTMENT_SUCCESS,
  INSTRUCTOR_CREATE_APPOINTMENT_FAIL,
  INSTRUCTOR_CREATE_APPOINTMENT_RESET,
  INSTRUCTOR_APPOINTMENT_DETAIL_REQUEST,
  INSTRUCTOR_APPOINTMENT_DETAIL_SUCCESS,
  INSTRUCTOR_APPOINTMENT_DETAIL_FAIL,
  ADD_APPOINTMENT,
  CLEAR_APPOINTMENT,
  REMOVE_APPOINTMENT,
  GET_APPOINTMENTS_REQUEST,
  GET_APPOINTMENTS_SUCCESS,
  GET_APPOINTMENTS_FAIL,
  APPOINTMENT_DELETE_REQUEST,
  APPOINTMENT_DELETE_SUCCESS,
  APPOINTMENT_DELETE_FAIL,
  APPOINTMENT_UPDATE_REQUEST,
  APPOINTMENT_UPDATE_SUCCESS,
  APPOINTMENT_UPDATE_FAIL,
  APPOINTMENT_UPDATE_RESET,
  APPOINTMENT_LIST_REQUEST,
  APPOINTMENT_LIST_SUCCESS,
  APPOINTMENT_LIST_FAIL,
} from "../constants/scheduleConstants"
import {
  CREATE_RAPPORT_REQUEST,
  CREATE_RAPPORT_SUCCESS,
  CREATE_RAPPORT_FAIL,
  CREATE_RAPPORT_RESET,
} from "../constants/enrollmentConstants"

export const createAppointmentReducer = (state = {}, action) => {
  switch (action.type) {
    case INSTRUCTOR_CREATE_APPOINTMENT_REQUEST:
      return { loading: true }
    case INSTRUCTOR_CREATE_APPOINTMENT_SUCCESS:
      return { loading: false, success: true, appointment: action.payload }
    case INSTRUCTOR_CREATE_APPOINTMENT_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_CREATE_APPOINTMENT_RESET:
      return {}
    default:
      return state
  }
}

export const appointmentDetailReducer = (state = { events: [] }, action) => {
  switch (action.type) {
    case INSTRUCTOR_APPOINTMENT_DETAIL_REQUEST:
      return { ...state, loading: true }
    case INSTRUCTOR_APPOINTMENT_DETAIL_SUCCESS:
      return { loading: false, appointment: action.payload }
    case INSTRUCTOR_APPOINTMENT_DETAIL_FAIL:
      return {}
    default:
      return state
  }
}

export const appointmentsReducer = (state = { events: [] }, action) => {
  switch (action.type) {
    case GET_APPOINTMENTS_REQUEST:
      return { ...state, loading: true }
    case GET_APPOINTMENTS_SUCCESS:
      return { loading: false, appointment: action.payload }
    case GET_APPOINTMENTS_FAIL:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const appointmentDeleteReducer = (state = {}, action) => {
  switch (action.type) {
    case APPOINTMENT_DELETE_REQUEST:
      return { loading: true }
    case APPOINTMENT_DELETE_SUCCESS:
      return { loading: false, success: true }
    case APPOINTMENT_DELETE_FAIL:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const appointmentUpdateReducer = (
  state = { appointment: {} },
  action
) => {
  switch (action.type) {
    case APPOINTMENT_UPDATE_REQUEST:
      return { loading: true }
    case APPOINTMENT_UPDATE_SUCCESS:
      return { loading: false, success: true, appointment: action.payload }
    case APPOINTMENT_UPDATE_FAIL:
      return { loading: false, error: action.payload }
    case APPOINTMENT_UPDATE_RESET:
      return { appointment: {} }
    default:
      return state
  }
}

export const appointmentListReducer = (state = { events: [] }, action) => {
  switch (action.type) {
    case APPOINTMENT_LIST_REQUEST:
      return {
        loading: true,
      }
    case APPOINTMENT_LIST_SUCCESS:
      return {
        loading: false,
        appointment: action.payload,
      }
    case APPOINTMENT_LIST_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}

export const createRapportReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_RAPPORT_REQUEST:
      return { loading: true }
    case CREATE_RAPPORT_SUCCESS:
      return { loading: false, success: true }
    case CREATE_RAPPORT_FAIL:
      return { loading: false, error: action.payload }
    case CREATE_RAPPORT_RESET:
      return {}
    default:
      return state
  }
}
