import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import {
  Row,
  Col,
  Image,
  ListGroup,
  Card,
  Button,
  Table,
  Container,
  Form,
} from "react-bootstrap"
import Rating from "../components/Rating"
import Message from "../components/Message"
import Loader from "../components/Loader"
import {
  getInstructorDetails,
  createInstructorReview,
  addFavoriteInstructor,
} from "../actions/instructorActions"
import { useParams } from "react-router-dom"
import Schedule from "../components/Schedule"

import { INSTRUCTOR_CREATE_REVIEW_RESET } from "../constants/instructorConstants"
import { getUserDetails } from "../actions/userActions"
import MyCalendar from "../components/MyCalendar"

const InstructorDetailsScreen = ({ history, match }) => {
  const [rating, setRating] = useState(0)
  const [comment, setComment] = useState("")
  const instructorId = match.params.id
  const dispatch = useDispatch()
  const instructorDetails = useSelector((state) => state.instructorDetails)
  const { loading, error, instructor } = instructorDetails

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  const userDetails = useSelector((state) => state.userDetails)
  const { user } = userDetails

  const instructorReviewCreate = useSelector(
    (state) => state.instructorReviewCreate
  )
  const {
    success: successInstructorReview,
    loading: loadingInstructorReview,
    error: errorInstructorReview,
  } = instructorReviewCreate

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const {
    loading: loadingInst,
    error: errorInst,
    instructorInfo,
  } = instructorLogin

  useEffect(() => {
    if (instructor._id !== instructorId) {
      dispatch(getInstructorDetails(instructorId))
    }

    if (!user || !user.firstName) {
      dispatch(getUserDetails("profile"))
    }
  }, [dispatch, match])

  useEffect(() => {
    if (successInstructorReview) {
      setRating(0)
      setComment("")
    }
    if (!instructor._id || instructor._id !== match.params.id) {
      dispatch(getInstructorDetails(match.params.id))
      dispatch({ type: INSTRUCTOR_CREATE_REVIEW_RESET })
    }
  }, [dispatch, match, successInstructorReview])

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(
      createInstructorReview(match.params.id, {
        rating,
        comment,
      })
    )
  }

  const submitFavorite = () => {
    dispatch(addFavoriteInstructor(match.params.id))
  }

  const Favorite = () => {
    if (instructor.favorites && instructor.favorites.length > 0) {
      return instructor.favorites.find((fav) => fav === userInfo._id) ? (
        <>
          <Button onClick={submitFavorite}>Add to favorite</Button>
        </>
      ) : (
        <>
          <Button onClick={submitFavorite}>Remove from favorite</Button>
        </>
      )
    }
  }
  const fav =
    userInfo &&
    instructor.favorites &&
    instructor.favorites.find((fa) => {
      if (fa === userInfo._id) {
        return (
          <>
            <Button>Add to favorite</Button>
          </>
        )
      }
    })
  //console.log(fav)

  //console.log(instructor.reviews)
  // if (instructor.reviews) {
  //   const rev = instructor.reviews.map((review) => {
  //     console.log(review.name)
  //     return <h1>{review.name}</h1>
  //   })
  // }

  return (
    <div>
      <Link className="btn btn-light ml-7 my-3" to="/">
        Go Back
      </Link>
      {instructorInfo && (
        <Link className="btn btn-light ml-7 my-3" to="/instructor-profile">
          Update
        </Link>
      )}
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <Container>
          <Row>
            <Col>
              <h4 className="d-flex justify-content-center align-items-center pb-6">
                {instructor.firstName} {instructor.lastName}
              </h4>
              <div className="d-flex justify-content-center">
                <Image
                  src={instructor.image}
                  alt={instructor.firstName}
                  style={{ width: "400px", height: "300px" }}
                  fluid
                />
              </div>

              {!instructorInfo && (
                <div className="d-flex justify-content-center pt-4">
                  {instructor.favorites && fav ? (
                    <>
                      <Button disabled={!userInfo} onClick={submitFavorite}>
                        Remove to favorite
                      </Button>
                    </>
                  ) : (
                    <>
                      <Button disabled={!userInfo} onClick={submitFavorite}>
                        Add to favorite
                      </Button>
                    </>
                  )}

                  {/* <i className="far fa-heart" onClick={submitFavorite}></i> */}

                  {/* <Button disabled={!userInfo} onClick={submitFavorite}>
                  Add to Favorites
                </Button> */}
                </div>
              )}
              <div className="pt-7">
                <h3 className="d-flex justify-content-center">Description</h3>
                <p>{instructor.description}</p>
              </div>
            </Col>
          </Row>
          <Row className="mt-12">
            <Col md={12}>
              <h2>Reviews</h2>
              {instructor.reviews === undefined && (
                <Message>No Reviews</Message>
              )}
              <ListGroup variant="flush">
                {instructor.reviews &&
                  instructor.reviews.map((review) => (
                    <ListGroup.Item key={review._id}>
                      <strong>{review.name}</strong>
                      <Rating value={review.rating} />
                      <p>{review.createdAt.substring(0, 10)}</p>
                      <p>{review.comment}</p>
                    </ListGroup.Item>
                  ))}

                <ListGroup.Item>
                  {!instructorInfo && (
                    <>
                      <h2>Write a Review</h2>
                      {successInstructorReview && (
                        <Message variant="success">
                          Review submitted successfully
                        </Message>
                      )}
                      {loadingInstructorReview && <Loader />}
                      {errorInstructorReview && (
                        <Message variant="danger">
                          {errorInstructorReview}
                        </Message>
                      )}
                      {userInfo ? (
                        <Form onSubmit={submitHandler}>
                          <Form.Group controlId="rating">
                            <Form.Label>Rating</Form.Label>
                            <Form.Control
                              as="select"
                              value={rating}
                              onChange={(e) => setRating(e.target.value)}
                            >
                              <option value="">Select...</option>
                              <option value="1">1 - Poor</option>
                              <option value="2">2 - Fair</option>
                              <option value="3">3 - Good</option>
                              <option value="4">4 - Very Good</option>
                              <option value="5">5 - Excellent</option>
                            </Form.Control>
                          </Form.Group>
                          <Form.Group controlId="comment">
                            <Form.Label>Comment</Form.Label>
                            <Form.Control
                              as="textarea"
                              row="3"
                              value={comment}
                              onChange={(e) => setComment(e.target.value)}
                            ></Form.Control>
                          </Form.Group>
                          <Button
                            disabled={loadingInstructorReview}
                            type="submit"
                            variant="primary"
                          >
                            Submit
                          </Button>
                        </Form>
                      ) : (
                        <Message>
                          Please <Link to="/login">sign in</Link> to write a
                          review{" "}
                        </Message>
                      )}
                    </>
                  )}
                </ListGroup.Item>
              </ListGroup>
              {/* <Table striped bordered hover>
                <tbody>
                  <tr>
                    <td>Country</td>
                    <td>
                      {/* {setTimeout(() => {
                        return Object.values(instructor.nationality)[1]
                      }, 5000)} */}
              {/* </td>
                  </tr>
                  <tr>
                    <td>Experience</td>
                    <td>{instructor.experience}</td>
                  </tr>
                  <tr>
                    <td>Speciality</td>
                    <td>{instructor.speciality}</td>
                  </tr>
                </tbody>
              </Table> */}
            </Col>
          </Row>
          <Row className="mt-8">
            <Col>
              {" "}
              <MyCalendar />{" "}
            </Col>
          </Row>
        </Container>
      )}
    </div>
  )
}

export default InstructorDetailsScreen
