import React, { useEffect } from "react"
import { LinkContainer } from "react-router-bootstrap"
import { Table, Button } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { listInstructors, deleteInstructor } from "../actions/instructorActions"

const InstructorListScreen = ({ history }) => {
  const dispatch = useDispatch()

  const instructorList = useSelector((state) => state.instructorList)
  const { loading, error, instructors } = instructorList

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const { instructorInfo } = instructorLogin

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  const instructorDelete = useSelector((state) => state.instructorDelete)
  const { success: successDelete } = instructorDelete

  useEffect(() => {
    if (userInfo && userInfo.isAdmin) {
      dispatch(listInstructors())
    } else {
      history.push("/login")
    }
  }, [dispatch, history, successDelete, userInfo])

  const deleteHandler = (id) => {
    if (window.confirm("Are you sure")) {
      dispatch(deleteInstructor(id))
    }
  }

  return (
    <>
      <h1>Users</h1>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <Table striped bordered hover responsive className="table-sm">
          <thead>
            <tr>
              <th>ID</th>
              <th>NAME</th>
              <th>EMAIL</th>
              <th>ADMIN</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {instructors.map((instructor) => (
              <tr key={instructor._id}>
                <td>{instructor._id}</td>
                <td>{instructor.name}</td>
                <td>
                  <a href={`mailto:${instructor.email}`}>{instructor.email}</a>
                </td>

                <td>
                  <LinkContainer
                    to={`/admin/instructor/${instructor._id}/edit`}
                  >
                    <Button variant="light" className="btn-sm">
                      <i className="fas fa-edit"></i>
                    </Button>
                  </LinkContainer>
                  <Button
                    variant="danger"
                    className="btn-sm"
                    onClick={() => deleteHandler(instructor._id)}
                  >
                    <i className="fas fa-trash"></i>
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      )}
    </>
  )
}

export default InstructorListScreen
