import { createStore, combineReducers, applyMiddleware } from "redux"
import thunk from "redux-thunk"
import { composeWithDevTools } from "redux-devtools-extension"
import {
  userLoginReducer,
  userRegisterReducer,
  userDetailsReducer,
  userUpdateProfileReducer,
  userListReducer,
  userDeleteReducer,
  userUpdateReducer,
} from "./reducers/userReducers"
import {
  instructorLoginReducer,
  instructorRegisterReducer,
  instructorDetailsReducer,
  instructorUpdateProfileReducer,
  instructorListReducer,
  instructorDeleteReducer,
  instructorUpdateReducer,
  instructorReviewCreateReducer,
  favoriteInst,
} from "./reducers/instructorReducers"
import {
  appointmentDetailReducer,
  createAppointmentReducer,
  appointmentsReducer,
  appointmentDeleteReducer,
  appointmentUpdateReducer,
  appointmentListReducer,
  createRapportReducer,
} from "./reducers/scheduleReducers"
import { cartReducer } from "./reducers/cartReducers"
import {
  enrollmentCreateReducer,
  enrollmentDetailsReducer,
  enrollmentListMyReducer,
  enrollmentListReducer,
  enrollmentPayReducer,
} from "./reducers/enrollmentReducers"

const reducer = combineReducers({
  userLogin: userLoginReducer,
  userRegister: userRegisterReducer,
  userDetails: userDetailsReducer,
  userUpdateProfile: userUpdateProfileReducer,
  userList: userListReducer,
  userDelete: userDeleteReducer,
  userUpdate: userUpdateReducer,
  instructorLogin: instructorLoginReducer,
  instructorRegister: instructorRegisterReducer,
  instructorDetails: instructorDetailsReducer,
  instructorUpdateProfile: instructorUpdateProfileReducer,
  instructorList: instructorListReducer,
  instructorDelete: instructorDeleteReducer,
  instructorUpdate: instructorUpdateReducer,
  instructorReviewCreate: instructorReviewCreateReducer,
  instructorFavorite: favoriteInst,
  createAppointment: createAppointmentReducer,
  appointmentDetail: appointmentDetailReducer,
  appointmentsDetails: appointmentsReducer,
  appointmentDelete: appointmentDeleteReducer,
  appointmentUpdate: appointmentUpdateReducer,
  appointmentList: appointmentListReducer,
  cart: cartReducer,
  enrollmentCreate: enrollmentCreateReducer,
  enrollmentDetails: enrollmentDetailsReducer,
  enrollmentListMy: enrollmentListMyReducer,
  enrollmentList: enrollmentListReducer,
  enrollmentPay: enrollmentPayReducer,
  createRapport: createRapportReducer,
})

const cartItemsFromStorage = localStorage.getItem("cartItems")
  ? JSON.parse(localStorage.getItem("cartItems"))
  : []

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null

const instructorInfoFromStorage = localStorage.getItem("instructorInfo")
  ? JSON.parse(localStorage.getItem("instructorInfo"))
  : null

const initialState = {
  cart: { cartItems: cartItemsFromStorage },
  userLogin: { userInfo: userInfoFromStorage },
  instructorLogin: { instructorInfo: instructorInfoFromStorage },
}

const middleware = [thunk]

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
)

export default store
