import React, { useEffect, useState } from "react"
import { Calendar, momentLocalizer } from "react-big-calendar"
import moment from "moment"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router-dom"

import Dialog from "@material-ui/core/Dialog"
import DialogActions from "@material-ui/core/DialogActions"
import DialogContent from "@material-ui/core/DialogContent"
import DialogContentText from "@material-ui/core/DialogContentText"
import DialogTitle from "@material-ui/core/DialogTitle"
import TextField from "@material-ui/core/TextField"
import Button from "@material-ui/core/Button"
import "date-fns"
import DateFnsUtils from "@date-io/date-fns"
import TimePicker from "material-ui/TimePicker"
import {
  createAppointmentInst,
  getInstAppointments,
  getAppointmentDetails,
  updateAppointment,
  deleteAppointment,
  listAppointments,
} from "../actions/scheduleActions"
import { APPOINTMENT_UPDATE_RESET } from "../constants/scheduleConstants"
import { addToCart, removeFromCart } from "../actions/cartActions"
import { useParams } from "react-router-dom"

import "react-big-calendar/lib/css/react-big-calendar.css"

const localizer = momentLocalizer(moment)

const MyCalendar = () => {
  let history = useHistory()
  const { id } = useParams()

  const [events, setEvents] = useState([])
  const [title, setTitle] = useState("")
  const [start, setStart] = useState(new Date("2014-08-18T21:11:54"))
  const [end, setEnd] = useState(new Date("2014-08-18T21:11:54"))
  const [desc, setDesc] = useState("")
  const [openSlot, setOpenSlot] = useState(false)
  const [openEvent, setOpenEvent] = useState(false)
  const [clickedEvent, setClickedEvent] = useState({})

  const dispatch = useDispatch()

  const cart = useSelector((state) => state.cart)
  const { cartItems } = cart

  const createAppointment = useSelector((state) => state.createAppointment)
  const { appointment, success, error } = createAppointment

  const appointmentsDetails = useSelector((state) => state.appointmentsDetails)
  const { appointment: app, loading: load, error: err } = appointmentsDetails

  const appointmentList = useSelector((state) => state.appointmentList)
  const { appointment: appoi, loading: l, error: er } = appointmentList

  const appointmentUpdate = useSelector((state) => state.appointmentUpdate)
  const {
    appointment: updatedApp,
    loading: loadingUpdate,
    error: errorUpdate,
    success: successUpdate,
  } = appointmentUpdate

  const appointmentDelete = useSelector((state) => state.appointmentDelete)
  const {
    loading: loadingDelete,
    error: errorDelete,
    success: successDelete,
  } = appointmentDelete

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const {
    loading: loadingInst,
    error: errorInst,
    instructorInfo,
  } = instructorLogin

  //console.log(app)

  useEffect(() => {
    if (successUpdate) {
      dispatch({ type: APPOINTMENT_UPDATE_RESET })
    }
    if (instructorInfo && !app) {
      dispatch(getInstAppointments())
    }
    if (userInfo && !appoi) {
      dispatch(listAppointments())
    }
    if (appoi) {
      let insevents = []
      for (let i = 0; i < appoi.length; i++) {
        appoi[i].start = moment.utc(appoi[i].start).toDate()
        appoi[i].end = moment.utc(appoi[i].end).toDate()
        if (appoi[i].instructor._id === id) {
          insevents.push(appoi[i])
        }
      }
      //console.log(insevents)
      setEvents(insevents)
    }
    if (successUpdate) {
      updatedApp.start = moment.utc(updatedApp.start).toDate()
      updatedApp.end = moment.utc(updatedApp.end).toDate()
      // console.log(updatedApp)
    }

    if (app) {
      for (let i = 0; i < app.length; i++) {
        app[i].start = moment.utc(app[i].start).toDate()
        app[i].end = moment.utc(app[i].end).toDate()
      }
      //console.log(app)
      setEvents(app)
    }
  }, [dispatch, app, successUpdate, appointment, successUpdate, appoi])

  const handleClose = () => {
    setOpenEvent(false)
    setOpenSlot(false)
    localStorage.removeItem("event")
  }

  const handleSlotSelected = (slotInfo) => {
    console.log("Real slotInfo", slotInfo)

    setTitle("")
    setDesc("")
    setStart(slotInfo.start)
    setEnd(slotInfo.end)
    setOpenSlot(true)
  }

  const handleEventSelected = (event) => {
    console.log("event", event)
    setOpenEvent(true)
    setClickedEvent(event)
    setStart(event.start)
    setEnd(event.end)
    setTitle(event.title)
    setDesc(event.desc)
    localStorage.setItem("event", JSON.stringify(event))
  }

  const handleStartTime = (event, date) => {
    setStart(date)
    console.log(date)
  }

  const handleEndTime = (event, date) => {
    setEnd(date)
  }

  const setNewAppointment = () => {
    dispatch(
      createAppointmentInst({
        _id: instructorInfo._id,
        title,
        start,
        end,
        desc,
      })
    )

    dispatch(getInstAppointments())
    handleClose()
  }

  const updateEvent = (event) => {
    const Event = localStorage.getItem("event")
    //console.log("Event", JSON.parse(Event))
    const oldEvent = JSON.parse(Event)

    dispatch(updateAppointment({ _id: oldEvent._id, title, start, end, desc }))
    if (successUpdate) {
      updatedApp.start = moment.utc(updatedApp.start).toDate()
      updatedApp.end = moment.utc(updatedApp.end).toDate()
      setEvents(updatedApp)
    }
    dispatch(getInstAppointments())
    handleClose()
  }

  const deleteEvent = () => {
    const Event = localStorage.getItem("event")
    //console.log("Event", JSON.parse(Event))
    const oldEvent = JSON.parse(Event)
    dispatch(deleteAppointment(oldEvent._id))
    dispatch(getInstAppointments())
    handleClose()
  }

  const handleBooking = () => {
    const Event = localStorage.getItem("event")
    //console.log("Event", JSON.parse(Event))
    const oldEvent = JSON.parse(Event)
    dispatch(addToCart(oldEvent._id))
    handleClose()
    history.push("/payment")
    console.log("appointment booked")
  }

  return (
    <div>
      <Calendar
        localizer={localizer}
        events={events}
        views={["month", "week", "day", "agenda"]}
        timeslots={2}
        defaultView="month"
        defaultDate={new Date()}
        selectable={true}
        onSelectEvent={(event) => handleEventSelected(event)}
        onSelectSlot={(slotInfo) => handleSlotSelected(slotInfo)}
        style={{ height: "100vh" }}
      />

      {instructorInfo && (
        <Dialog
          open={openSlot}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{`Create an appointment on ${moment(
            start
          ).format("MMMM Do YYYY")}`}</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              label="Title"
              type="text"
              onChange={(e) => setTitle(e.target.value)}
            />
            <br />
            <TextField
              autoFocus
              margin="dense"
              label="Description"
              type="text"
              onChange={(e) => setDesc(e.target.value)}
            />
            <TimePicker
              format="ampm"
              floatingLabelText="Start Time"
              minutesStep={5}
              value={start}
              onChange={handleStartTime}
            />
            <TimePicker
              format="ampm"
              floatingLabelText="End Time"
              minutesStep={5}
              value={end}
              onChange={handleEndTime}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="secondary">
              Cancel
            </Button>
            <Button
              type="submit"
              onClick={setNewAppointment}
              color="primary"
              autoFocus
            >
              Submit
            </Button>
          </DialogActions>
        </Dialog>
      )}
      {instructorInfo && (
        <Dialog
          open={openEvent}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{`View/Edit Appointment of ${moment(
            start
          ).format("MMMM Do YYYY")}`}</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              label="Title"
              type="text"
              defaultValue={title}
              onChange={(e) => setTitle(e.target.value)}
            />
            <br />
            <TextField
              autoFocus
              margin="dense"
              label="Description"
              type="text"
              defaultValue={desc}
              onChange={(e) => setDesc(e.target.value)}
            />

            <TimePicker
              format="ampm"
              floatingLabelText="Start Time"
              minutesStep={5}
              value={start}
              onChange={handleStartTime}
            />
            <TimePicker
              format="ampm"
              floatingLabelText="End Time"
              minutesStep={5}
              value={end}
              onChange={handleEndTime}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="secondary">
              Cancel
            </Button>
            <Button onClick={deleteEvent} color="secondary" autoFocus>
              Delete
            </Button>
            <Button onClick={updateEvent} color="primary" autoFocus>
              Confirm Edit
            </Button>
          </DialogActions>
        </Dialog>
      )}

      {userInfo && (
        <Dialog
          open={openEvent}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{`Book an Appointment on ${moment(
            start
          ).format("MMMM Do YYYY")}`}</DialogTitle>
          <DialogContent>
            <DialogContentText>Book an appointment</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="secondary">
              Cancel
            </Button>
            <Button onClick={handleBooking} color="primary" autoFocus>
              Book
            </Button>
          </DialogActions>
        </Dialog>
      )}
    </div>
  )
}

export default MyCalendar
