import React, { useEffect } from "react"
import { Row, Col } from "react-bootstrap"
import Message from "../components/Message"
import Loader from "../components/Loader"
import Instructor from "../components/Instructor"
import { listInstructors } from "../actions/instructorActions"
import { useDispatch, useSelector } from "react-redux"
import Paginate from "../components/Paginate"

const InstructorsScreen = ({ match }) => {
  const keyword = match.params.keyword
  const pageNumber = match.params.pageNumber || 1
  const dispatch = useDispatch()

  const instructorList = useSelector((state) => state.instructorList)
  const { loading, error, instructors, page, pages } = instructorList

  useEffect(() => {
    dispatch(listInstructors(keyword, pageNumber))
  }, [dispatch, keyword, pageNumber])
  return (
    <div>
      <h1 className="pl-5">Our Tutors</h1>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <>
          <Row>
            {instructors.instructors &&
              instructors.instructors.map((instructor) => (
                <Col key={instructor._id} sm={12} md={6} lg={4} xl={3}>
                  <Instructor instructor={instructor} />
                </Col>
              ))}
          </Row>
          <Paginate
            pages={pages}
            page={page}
            keyword={keyword ? keyword : ""}
          />
        </>
      )}
    </div>
  )
}

export default InstructorsScreen
