import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Form, Button, Row, Col } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import Message from "../components/Message"
import Loader from "../components/Loader"
import FormContainer from "../components/FormContainer"
import {
  getInstructorDetails,
  updateInstructorProfile,
} from "../actions/instructorActions"
import FileBase from "react-file-base64"
import "react-bootstrap-country-select/dist/react-bootstrap-country-select.css"
import CountrySelect from "react-bootstrap-country-select"
import { INSTRUCTOR_UPDATE_PROFILE_RESET } from "../constants/instructorConstants"

const InstructorProfileScreen = ({ location, history }) => {
  const [firstName, setFirstName] = useState("")
  const [lastName, setLastName] = useState("")
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [confirmPassword, setConfirmPassword] = useState("")
  const [description, setDescription] = useState("")
  const [experience, setExperience] = useState("")
  const [nationality, setNationality] = useState("")
  const [speciality, setSpeciality] = useState("")
  const [image, setImage] = useState("")

  const [value, setValue] = useState(null)

  const [message, setMessage] = useState(null)

  const dispatch = useDispatch()

  const instructorDetails = useSelector((state) => state.instructorDetails)
  const { loading, error, instructor } = instructorDetails

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const { instructorInfo } = instructorLogin

  const instructorUpdateProfile = useSelector(
    (state) => state.instructorUpdateProfile
  )
  const { success } = instructorUpdateProfile

  const redirect = location.search ? location.search.split("=")[1] : "/"

  useEffect(() => {
    if (!instructorInfo) {
      history.push("/login-instructor")
    } else {
      if (!instructor || !instructor.firstName || success) {
        dispatch({ type: INSTRUCTOR_UPDATE_PROFILE_RESET })
        dispatch(getInstructorDetails("profile"))
      } else {
        setFirstName(instructor.firstName)
        setLastName(instructor.lastName)
        setDescription(instructor.description)
        setExperience(instructor.experience)
        setSpeciality(instructor.speciality)
        setEmail(instructor.email)
      }
    }
  }, [dispatch, history, instructorInfo, instructor, success])

  const submitHandler = (e) => {
    e.preventDefault()
    if (password !== confirmPassword) {
      setMessage("Passwords do not match")
    } else {
      dispatch(
        updateInstructorProfile(
          firstName,
          lastName,
          email,
          password,
          nationality,
          description,
          speciality,
          experience,
          image
        )
      )
    }
  }

  return (
    <FormContainer>
      <h1>Profile</h1>
      {message && <Message variant="danger">{message}</Message>}
      {error && <Message variant="danger">{error}</Message>}
      {loading && <Loader />}

      <Form onSubmit={submitHandler}>
        <Form.Label>Nationality</Form.Label>
        <CountrySelect value={value} onChange={setValue} flags={true} />
        <Form.Group controlId="first-name">
          <Form.Label>First Name</Form.Label>
          <Form.Control
            type="name"
            placeholder="Enter first name"
            value={firstName}
            onChange={(e) => setFirstName(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Form.Group controlId="last-name">
          <Form.Label>Last Name</Form.Label>
          <Form.Control
            type="name"
            placeholder="Enter last name"
            value={lastName}
            onChange={(e) => setLastName(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="email">
          <Form.Label>Email Address</Form.Label>
          <Form.Control
            type="email"
            placeholder="Enter email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="description">
          <Form.Label>Description</Form.Label>
          <Form.Control
            as="textarea"
            row={3}
            placeholder="Enter description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="experience">
          <Form.Label>experience</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter experience"
            value={experience}
            onChange={(e) => setExperience(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="speciality">
          <Form.Label>Speciality</Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter speciality"
            value={speciality}
            onChange={(e) => setSpeciality(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            placeholder="Enter password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="confirmPassword">
          <Form.Label>Confirm Password</Form.Label>
          <Form.Control
            type="password"
            placeholder="Confirm password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <FileBase
          type="file"
          multiple={false}
          onDone={({ base64 }) => setImage(base64)}
        />

        <Button type="submit" variant="primary">
          Update
        </Button>
      </Form>
    </FormContainer>
  )
}

export default InstructorProfileScreen
