import axios from "axios"
import {
  CREATE_ENROLLMENT_REQUEST,
  CREATE_ENROLLMENT_SUCCESS,
  CREATE_ENROLLMENT_FAIL,
  ENROLLMENT_DETAILS_REQUEST,
  ENROLLMENT_DETAILS_SUCCESS,
  ENROLLMENT_DETAILS_FAIL,
  ENROLLMENT_LIST_MY_REQUEST,
  ENROLLMENT_LIST_MY_SUCCESS,
  ENROLLMENT_LIST_MY_FAIL,
  ENROLLMENT_LIST_REQUEST,
  ENROLLMENT_LIST_SUCCESS,
  ENROLLMENT_LIST_FAIL,
  ORDER_PAY_FAIL,
  ORDER_PAY_SUCCESS,
  ORDER_PAY_REQUEST,
  CREATE_RAPPORT_REQUEST,
  CREATE_RAPPORT_SUCCESS,
  CREATE_RAPPORT_FAIL,
} from "../constants/enrollmentConstants"
import { CART_CLEAR_ITEMS } from "../constants/cartConstants"
import { logout } from "./userActions"
import { logoutInstructor } from "./instructorActions"

export const createEnrollment = (enrollment) => async (dispatch, getState) => {
  try {
    dispatch({
      type: CREATE_ENROLLMENT_REQUEST,
    })

    const {
      userLogin: { userInfo },
    } = getState()

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    }

    const { data } = await axios.post(`/api/enrollment`, enrollment, config)

    dispatch({
      type: CREATE_ENROLLMENT_SUCCESS,
      payload: data,
    })
    dispatch({
      type: CART_CLEAR_ITEMS,
      payload: data,
    })
    localStorage.removeItem("cartItems")
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      dispatch(logout())
    }
    dispatch({
      type: CREATE_ENROLLMENT_FAIL,
      payload: message,
    })
  }
}

