export const pay = (enrollmentId, paymentResult) => async (
  dispatch,
  getState
) => {
  try {
    dispatch({
      type: ORDER_PAY_REQUEST,
    })

    const {
      userLogin: { userInfo },
    } = getState()

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    }

    const { data } = await axios.put(
      `/api/enrollment/${enrollmentId}/pay`,
      paymentResult,
      config
    )

    dispatch({
      type: ORDER_PAY_SUCCESS,
      payload: data,
    })
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      dispatch(logout())
    }
    dispatch({
      type: ORDER_PAY_FAIL,
      payload: message,
    })
  }
}

export const createrapportInst = (enrollmentId, rapport) => async (
  dispatch,
  getState
) => {
  try {
    dispatch({ type: CREATE_RAPPORT_REQUEST })
    const {
      instructorLogin: { instructorInfo },
    } = getState()
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${instructorInfo.token}`,
      },
    }
    const { data } = await axios.post(
      `/api/enrollment/${enrollmentId}/reviews`,
      rapport,
      config
    )

    dispatch({
      type: CREATE_RAPPORT_SUCCESS,
      payload: data,
    })
  } catch (error) {
    const message =
      error.response && error.response.data.message
        ? error.response.data.message
        : error.message
    if (message === "Not authorized, token failed") {
      dispatch(logoutInstructor())
    }
    dispatch({
      type: CREATE_RAPPORT_FAIL,
      payload: message,
    })
  }
}
