import React, { useEffect } from "react"
import { Link } from "react-router-dom"
import { useDispatch, useSelector } from "react-redux"
import { Row, Col, ListGroup, Image, Form, Button, Card } from "react-bootstrap"
import Message from "../components/Message"
import { addToCart, removeFromCart } from "../actions/cartActions"
import moment from "moment"
import { createEnrollment } from "../actions/enrollmentActions"
import { CREATE_ENROLLMENT_RESET } from "../constants/enrollmentConstants"
import { USER_DETAILS_RESET } from "../constants/userConstants"
import CheckoutSteps from "../components/CheckoutSteps"

const BookedLessons = ({ history }) => {
  const dispatch = useDispatch()

  const cart = useSelector((state) => state.cart)
  const { cartItems } = cart

  const enrollmentCreate = useSelector((state) => state.enrollmentCreate)
  const { enrollment, success, error } = enrollmentCreate
  //console.log(enrollment)

  useEffect(() => {
    if (success) {
      history.push(`/lessons/${enrollment._id}`)
      dispatch({ type: USER_DETAILS_RESET })
      dispatch({ type: CREATE_ENROLLMENT_RESET })
    }
    // eslint-disable-next-line
  }, [history, success, dispatch])

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id))
  }

  cart.totalPrice = Number(10)

  const checkoutHandler = () => {
    dispatch(
      createEnrollment({
        bookings: cart.cartItems,
        paymentMethod: cart.paymentMethod,
        totalPrice: cart.totalPrice,
      })
    )
  }

  return (
    <>
      <CheckoutSteps step1 step2 step3 />
      <Row>
        <Col md={8}>
          <h1 className="ml-6 mt-4 ">Added Lessons</h1>
          {cartItems.length === 0 ? (
            <Message>
              Your don't have any lessons <Link to="/">Go Back</Link>
            </Message>
          ) : (
            <ListGroup variant="flush">
              {cartItems.map((item) => (
                <ListGroup.Item key={item.schedule}>
                  <Row>
                    <Col md={2}>
                      <h5>{item.title}</h5>
                    </Col>
                    <Col md={2}>{item.desc}</Col>
                    <Col md={3}>
                      {moment(item.start).format("MMMM Do YYYY, h:mm:ss a")}
                    </Col>
                    <Col md={3}>
                      {moment(item.end).format("MMMM Do YYYY, h:mm:ss a")}
                    </Col>
                    <Col md={2}>
                      <Button
                        type="button"
                        variant="light"
                        onClick={() => removeFromCartHandler(item.schedule)}
                      >
                        <i className="fas fa-trash"></i>
                      </Button>
                    </Col>
                  </Row>
                </ListGroup.Item>
              ))}
            </ListGroup>
          )}
        </Col>
        <Col md={4}>
          <Card className="d-flex justify-content-center align-items-center pt-12">
            <ListGroup variant="flush">
              <ListGroup.Item>
                <Button
                  type="button"
                  className="btn-block"
                  disabled={cartItems.length === 0}
                  onClick={checkoutHandler}
                >
                  Proceed To Book These Lessons
                </Button>
              </ListGroup.Item>
            </ListGroup>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default BookedLessons
