import React from "react"
import { Route } from "react-router-dom"
import { LinkContainer } from "react-router-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { logout } from "../actions/userActions"
import { logoutInstructor } from "../actions/instructorActions"

import { Navbar, Nav, Container, NavDropdown } from "react-bootstrap"

const Header = () => {
  const dispatch = useDispatch()

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  const instructorLogin = useSelector((state) => state.instructorLogin)
  const { loading, error, instructorInfo } = instructorLogin

  const logoutHandler = () => {
    dispatch(logout())
  }
  const logoutInstructorHandler = () => {
    dispatch(logoutInstructor())
  }
  return (
    <header className="position-sticky">
      <Navbar bg="dark" variant="dark" expand="lg" collapseOnSelect>
        <Container>
          <LinkContainer to="/">
            <Navbar.Brand>E-LEARNING</Navbar.Brand>
          </LinkContainer>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ml-auto">
              {!userInfo && instructorInfo && (
                <>
                  <LinkContainer to="/calendar">
                    <Nav.Link>My Schedule</Nav.Link>
                  </LinkContainer>
                  <LinkContainer to="/instructor-lessons">
                    <Nav.Link>My Lessons</Nav.Link>
                  </LinkContainer>
                </>
              )}
              {userInfo && !userInfo.isAdmin && !instructorInfo && (
                <LinkContainer to="/instructors">
                  <Nav.Link>Our tutors</Nav.Link>
                </LinkContainer>
              )}
              <LinkContainer to="/contact">
                <Nav.Link>Contact us</Nav.Link>
              </LinkContainer>
              {userInfo ? (
                <>
                  <LinkContainer to="/dashboard">
                    <Nav.Link>Dashboard</Nav.Link>
                  </LinkContainer>
                  <a onClick={logoutHandler}>
                    <i
                      className="fas fa-sign-out-alt fa-2x pl-3 mt-1"
                      style={{ cursor: "pointer" }}
                    ></i>
                  </a>
                </>
              ) : (
                <>
                  {!instructorInfo && (
                    <>
                      <LinkContainer to="/login">
                        <Nav.Link href="#link">
                          <i className="fas fa-sign-in-alt pl-3"></i> Login
                        </Nav.Link>
                      </LinkContainer>
                      {/* <LinkContainer to="/register">
                        <Nav.Link>
                          <i className="fas fa-user-plus"></i> Register
                        </Nav.Link>
                      </LinkContainer> */}
                    </>
                  )}
                </>
              )}
              {instructorInfo && !userInfo ? (
                <>
                  <LinkContainer to={`/instructor/${instructorInfo._id}`}>
                    <Nav.Link>My Profile</Nav.Link>
                  </LinkContainer>
                  <a onClick={logoutInstructorHandler}>
                    <i
                      className="fas fa-sign-out-alt fa-2x pl-3 mt-1"
                      style={{ cursor: "pointer" }}
                    ></i>
                  </a>
                </>
              ) : (
                <>
                  {!userInfo && (
                    <>
                      <LinkContainer to="/login-instructor">
                        <Nav.Link href="#link">
                          <i className="fas fa-sign-in-alt pl-3"></i> Tutors
                          Portal
                        </Nav.Link>
                      </LinkContainer>
                      <LinkContainer to="/admin-login">
                        <Nav.Link href="#link">
                          <i className="fas fa-sign-in-alt pl-3"></i> Admin
                        </Nav.Link>
                      </LinkContainer>
                    </>
                  )}
                </>
              )}
              {/* {userInfo && userInfo.isAdmin && (
                <NavDropdown title="Admin" id="adminmenu">
                  <LinkContainer to="/admin/userlist">
                    <NavDropdown.Item>Users</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/admin/instructorlist">
                    <NavDropdown.Item>Products</NavDropdown.Item>
                  </LinkContainer>
                </NavDropdown>
              )} */}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  )
}

export default Header
