import React, { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import { Row, Col, ListGroup, Image, Card, Button } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import Message from "../components/Message"
import Loader from "../components/Loader"
import { listMyEnrollments } from "../actions/enrollmentActions"
import moment from "moment"
import { useHistory } from "react-router-dom"

const StudentLessonsScreen = () => {
  let history = useHistory()
  const dispatch = useDispatch()

  const enrollmentListMy = useSelector((state) => state.enrollmentListMy)
  const { enrollments, loading, error } = enrollmentListMy

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  useEffect(() => {
    if (enrollments.length === 0) {
      dispatch(listMyEnrollments())
    }

    if (!userInfo) {
      history.push("/login")
    }
  }, [dispatch])

  return loading ? (
    <Loader />
  ) : error ? (
    <Message variant="danger">{error}</Message>
  ) : (
    <>
      <h1 className="ml-3">Lessons </h1>
      <Row>
        <Col md={12}>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h2 className="ml-3">Student</h2>
              <p>
                <strong>Name: </strong> {userInfo.firstName} {userInfo.lastName}
              </p>
              {/* <p>
                <strong>Email: </strong>{" "}
                <a href={`mailto:${enrollment.user.email}`}>
                  {enrollment.user.email}
                </a>
              </p> */}
              <ListGroup.Item>
                <h2>Lessons</h2>
                {enrollments.length === 0 ? (
                  <Message>No Lessons</Message>
                ) : (
                  <ListGroup variant="flush">
                    {enrollments.map((item, index) => (
                      <ListGroup.Item key={index}>
                        {item.bookings &&
                          item.bookings.map((lesson, index) => (
                            <Row key={index}>
                              {console.log(lesson)}
                              <Col>{lesson.title}</Col>
                              <Col>{lesson.desc}</Col>
                              <Col md={3}>
                                {moment(lesson.start).format(
                                  "MMMM Do YYYY, h:mm:ss a"
                                )}
                              </Col>
                              <Col md={3}>
                                {moment(lesson.start).format(
                                  "MMMM Do YYYY, h:mm:ss a"
                                )}
                              </Col>

                              <Col md={2}>
                                <Button href={`/video/${lesson._id}`}>
                                  Go to meeting
                                </Button>
                              </Col>
                              <Col md={2}>
                                <Button
                                  href={`${lesson.schedule}/rapport`}
                                  className="btn btn-light"
                                >
                                  Rapport
                                </Button>
                              </Col>
                            </Row>
                          ))}
                      </ListGroup.Item>
                    ))}
                  </ListGroup>
                )}
              </ListGroup.Item>
            </ListGroup.Item>
          </ListGroup>
        </Col>
      </Row>
    </>
  )
}

export default StudentLessonsScreen
