import {
  INSTRUCTOR_DETAILS_FAIL,
  INSTRUCTOR_DETAILS_REQUEST,
  INSTRUCTOR_DETAILS_SUCCESS,
  INSTRUCTOR_LOGIN_FAIL,
  INSTRUCTOR_LOGIN_REQUEST,
  INSTRUCTOR_LOGIN_SUCCESS,
  INSTRUCTOR_LOGOUT,
  INSTRUCTOR_REGISTER_FAIL,
  INSTRUCTOR_REGISTER_REQUEST,
  INSTRUCTOR_REGISTER_SUCCESS,
  INSTRUCTOR_UPDATE_PROFILE_FAIL,
  INSTRUCTOR_UPDATE_PROFILE_REQUEST,
  INSTRUCTOR_UPDATE_PROFILE_SUCCESS,
  INSTRUCTOR_UPDATE_PROFILE_RESET,
  INSTRUCTOR_DETAILS_RESET,
  INSTRUCTOR_LIST_FAIL,
  INSTRUCTOR_LIST_SUCCESS,
  INSTRUCTOR_LIST_REQUEST,
  INSTRUCTOR_LIST_RESET,
  INSTRUCTOR_DELETE_REQUEST,
  INSTRUCTOR_DELETE_SUCCESS,
  INSTRUCTOR_DELETE_FAIL,
  INSTRUCTOR_UPDATE_FAIL,
  INSTRUCTOR_UPDATE_SUCCESS,
  INSTRUCTOR_UPDATE_REQUEST,
  INSTRUCTOR_UPDATE_RESET,
  INSTRUCTOR_CREATE_REVIEW_REQUEST,
  INSTRUCTOR_CREATE_REVIEW_SUCCESS,
  INSTRUCTOR_CREATE_REVIEW_FAIL,
  INSTRUCTOR_CREATE_REVIEW_RESET,
  FAVORITE_INSTRUCTOR,
} from "../constants/instructorConstants"

export const instructorLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case INSTRUCTOR_LOGIN_REQUEST:
      return { loading: true }
    case INSTRUCTOR_LOGIN_SUCCESS:
      return { loading: false, instructorInfo: action.payload }
    case INSTRUCTOR_LOGIN_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_LOGOUT:
      return {}
    default:
      return state
  }
}

export const instructorRegisterReducer = (state = {}, action) => {
  switch (action.type) {
    case INSTRUCTOR_REGISTER_REQUEST:
      return { loading: true }
    case INSTRUCTOR_REGISTER_SUCCESS:
      return { loading: false, instructorInfo: action.payload }
    case INSTRUCTOR_REGISTER_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_LOGOUT:
      return {}
    default:
      return state
  }
}

export const instructorDetailsReducer = (
  state = { instructor: {} },
  action
) => {
  switch (action.type) {
    case INSTRUCTOR_DETAILS_REQUEST:
      return { ...state, loading: true }
    case INSTRUCTOR_DETAILS_SUCCESS:
      return { loading: false, instructor: action.payload }
    case INSTRUCTOR_DETAILS_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_DETAILS_RESET:
      return { instructor: {} }
    default:
      return state
  }
}

export const instructorUpdateProfileReducer = (state = {}, action) => {
  switch (action.type) {
    case INSTRUCTOR_UPDATE_PROFILE_REQUEST:
      return { loading: true }
    case INSTRUCTOR_UPDATE_PROFILE_SUCCESS:
      return { loading: false, success: true, instructorInfo: action.payload }
    case INSTRUCTOR_UPDATE_PROFILE_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_UPDATE_PROFILE_RESET:
      return {}
    default:
      return state
  }
}

export const instructorListReducer = (state = { instructors: [] }, action) => {
  switch (action.type) {
    case INSTRUCTOR_LIST_REQUEST:
      return { loading: true }
    case INSTRUCTOR_LIST_SUCCESS:
      return {
        loading: false,
        instructors: action.payload,
        pages: action.payload.pages,
        page: action.payload.page,
      }
    case INSTRUCTOR_LIST_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_LIST_RESET:
      return { instructors: [] }
    default:
      return state
  }
}

export const instructorDeleteReducer = (state = {}, action) => {
  switch (action.type) {
    case INSTRUCTOR_DELETE_REQUEST:
      return { loading: true }
    case INSTRUCTOR_DELETE_SUCCESS:
      return { loading: false, success: true }
    case INSTRUCTOR_DELETE_FAIL:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const instructorUpdateReducer = (state = { instructor: {} }, action) => {
  switch (action.type) {
    case INSTRUCTOR_UPDATE_REQUEST:
      return { loading: true }
    case INSTRUCTOR_UPDATE_SUCCESS:
      return { loading: false, success: true }
    case INSTRUCTOR_UPDATE_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_UPDATE_RESET:
      return {
        instructor: {},
      }
    default:
      return state
  }
}

export const instructorReviewCreateReducer = (state = {}, action) => {
  switch (action.type) {
    case INSTRUCTOR_CREATE_REVIEW_REQUEST:
      return { loading: true }
    case INSTRUCTOR_CREATE_REVIEW_SUCCESS:
      return { loading: false, success: true }
    case INSTRUCTOR_CREATE_REVIEW_FAIL:
      return { loading: false, error: action.payload }
    case INSTRUCTOR_CREATE_REVIEW_RESET:
      return {}
    default:
      return state
  }
}

export const favoriteInst = (instructors = [], action) => {
  switch (action.type) {
    case FAVORITE_INSTRUCTOR:
      return instructors.map((instructor) =>
        instructor._id === action.payload._id ? action.payload : instructor
      )
    default:
      return instructors
  }
}
