import { BrowserRouter as Router, Route } from "react-router-dom"
import { Container } from "react-bootstrap"
import Header from "./components/Header"
import LoginScreen from "./screens/LoginScreen"
import RegisterScreen from "./screens/RegisterScreen"
import HomeScreen from "./screens/HomeScreen"
import DashboardScreen from "./screens/DashboardScreen"
import InstructorLoginScreen from "./screens/InstructorLoginScreen"
import InstructorRegisterScreen from "./screens/InstructorRegisterScreen"
import ContactUsScreen from "./screens/ContactUsScreen"
import InstructorsScreen from "./screens/InstructorsScreen"
import ProfileScreen from "./screens/ProfileScreen"
import InstructorDetailsScreen from "./screens/InstructorDetailsScreen"
import InstructorProfileScreen from "./screens/InstructorProfileScreen"
import FavoriteInstructorScreen from "./screens/FavoriteInstructorScreen"
import UserListScreen from "./screens/UserListScreen"
import UserEditScreen from "./screens/UserEditScreen"
import InstructorListScreen from "./screens/InstructorListScreen"
import InstructorEditScreen from "./screens/InstructorEditScreen"
//import CalendarBig from "./components/CalendarBig"
import MyCalendar from "./components/MyCalendar"
import BookedLessons from "./screens/BookedLessons"
import LessonsScreen from "./screens/LessonsScreen"
import InstructorLessons from "./screens/InstructorLessons"
import StudentLessonsScreen from "./screens/StudentLessonsScreen"
import PaymentScreen from "./screens/PaymentScreen"
import AdminDashboardScreen from "./screens/AdminDashboardScreen"
import AdminLoginScreen from "./screens/AdminLoginScreen"
import { useDispatch, useSelector } from "react-redux"
import AdminSidebar from "./components/AdminSidebar"
import Log from "./screens/Log"
import Schedule from "./components/Schedule"
import Sign from "./components/Sign"
import RapportScreen from "./screens/RapportScreen"
import "./App.css"

function App() {
  const userLogin = useSelector((state) => state.userLogin)
  const { loading, error, userInfo } = userLogin
  return (
    <Router>
      <Header />

      <main className="py-3">
        <Route exact path="/" component={HomeScreen} />
        <Route path="/dashboard" component={DashboardScreen} />
        <Route
          path="/dashboard/page/:pageNumber"
          component={DashboardScreen}
          exact
        />
        <Route path="/login" component={Schedule} />
        <Route path="/register" component={RegisterScreen} />
        <Route path="/profile" component={ProfileScreen} />
        <Route path="/login-instructor" component={Sign} />
        <Route path="/contact" component={ContactUsScreen} />
        <Route
          path="/register-instructor"
          component={InstructorRegisterScreen}
        />
        <Route path="/instructor-profile" component={InstructorProfileScreen} />
        <Route exact path="/instructors" component={InstructorsScreen} />
        <Route
          exact
          path="/instructors/search/:keyword"
          component={InstructorsScreen}
          exact
        />
        <Route
          exact
          path="/instructors/page/:pageNumber"
          component={InstructorsScreen}
          exact
        />
        <Route path="/instructor/:id" component={InstructorDetailsScreen} />
        <Route path="/favorite-tutors" component={FavoriteInstructorScreen} />
        <Route path="/admin/userlist" component={UserListScreen} />
        <Route path="/admin/user/:id/edit" component={UserEditScreen} />
        <Route path="/admin/instructorlist" component={InstructorListScreen} />
        <Route
          path="/admin/instructor/:id/edit"
          component={InstructorEditScreen}
        />
        <Route path="/lessons" component={BookedLessons} exact />
        <Route path="/lessons/:id" component={LessonsScreen} />
        <Route path="/instructor-lessons" component={InstructorLessons} />
        <Route path="/student-lessons" component={StudentLessonsScreen} />
        <Route path="/calendar" component={MyCalendar} />
        <Route path="/payment" component={PaymentScreen} />
        <Route path="/admin-dashboard" component={AdminDashboardScreen} />
        <Route path="/admin-login" component={AdminLoginScreen} />
        <Route path="/log" component={Log} />
        <Route path="/logi" component={Schedule} />
        <Route path="/si" component={Sign} />
        <Route path="/:id/rapport" component={RapportScreen} />
      </main>
    </Router>
  )
}

export default App
