import React, { useEffect } from "react"
import Loader from "../components/Loader"
import Message from "../components/Message"
import { useDispatch, useSelector } from "react-redux"
import {
  getInstructorDetails,
  listInstructors,
} from "../actions/instructorActions"
import { getUserDetails } from "../actions/userActions"
import Instructor from "../components/Instructor"
import { Container, Row, Col } from "react-bootstrap"

const FavoriteInstructorScreen = ({ match }) => {
  const dispatch = useDispatch()

  const instructorList = useSelector((state) => state.instructorList)
  const { loading, error, instructors } = instructorList

  const instructorDetails = useSelector((state) => state.instructorDetails)
  const { loading: load, error: err, instructor } = instructorDetails

  const userDetails = useSelector((state) => state.userDetails)
  const { user } = userDetails

  const userLogin = useSelector((state) => state.userLogin)
  const { userInfo } = userLogin

  // const fav =
  //   instructors &&
  //   instructors.map((instructor) => {
  //     if (inst.favorites.includes(userInfo._id) == true) {
  //       console.log(inst)
  //     }
  //   })

  useEffect(() => {
    if (instructor._id !== match.params.id) {
      dispatch(getInstructorDetails(match.params.id))
    }
    dispatch(listInstructors())

    if (!user || !user.firstName) {
      dispatch(getUserDetails("profile"))
    }
  }, [dispatch, match])

  return (
    <div>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <>
          {/* <Row>
            {instructors.map((instructor) => (
              <Col key={instructor._id} sm={12} md={6} lg={4} xl={3}>
                <Instructor key={instructor._id} instructor={instructor} />
              </Col>
            ))}
          </Row> */}
          <Row>
            {instructors.instructors &&
              instructors.instructors.map((instructor) => {
                if (instructor.favorites.includes(userInfo._id) == true) {
                  return (
                    <Col key={instructor._id} sm={12} md={6} lg={4} xl={3}>
                      <Instructor
                        key={instructor._id}
                        instructor={instructor}
                      />
                    </Col>
                  )
                }
              })}
          </Row>
        </>
      )}
    </div>
  )
}

export default FavoriteInstructorScreen
